import { FaEye, FaEyeSlash } from 'react-icons/fa';

export default function InputField({
  placeholder,
  type = 'text',
  value,
  onChange,
  showToggle = false,
  onToggle,
  isPasswordVisible,
}) {
  return (
    <div className="relative">
      <input
        type={type}
        placeholder={placeholder}
        value={value}
        onChange={onChange}
        className="w-full px-4 py-2 bg-gray-100 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#2ED3C3] pr-10"
      />
      {showToggle && (
        <button
          type="button"
          onClick={onToggle}
          className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-500 hover:text-gray-700"
        >
          {isPasswordVisible ? <FaEyeSlash /> : <FaEye />}
        </button>
      )}
    </div>
  );
}

